<template>
    <div class="logo">
        <transition name="fade">
            <span v-if="isCollapse" class="logo_title is-bold" key="0" :class="{'is-test': !type, 'is-img': type}">
                <template v-if="type">
                    <img src="" width="40" height="40" alt="">
                </template>
                <template v-else>
                    T
                </template>
            </span>
        </transition>
        <transition-group>
            <template v-if="!isCollapse">
                <span class="logo_title is-bold" key="1">Test</span>
                <span class="logo_subtitle" key="2"> By Author</span>
            </template>
        </transition-group>
    </div>
</template>
<script>
export default {
    name: 'logo',
    data() {
        return {
            type: false
        }
    },
    props: ['isCollapse'],
}
</script>
<style scoped="scoped" lang="scss">
.fade-leave-active{
    transition: opacity 0.2s;
}
.fade-enter-active{
    transition: opacity 2.5s;
}
.fade-enter, .fade-leave-to{
    opacity: 0;
}
.logo{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    line-height: 64px;
    background: #473C88;
    color: #fdfdfd;
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    overflow: hidden;
    box-sizing: border-box;
}
.logo_title{
    padding: 0 5px 0 0;
    color: #42b983;
    font-size: 20px;
    &.is-bold{
        font-weight: 700;
    }
}
.is-text{
    position: absolute;
    top: 0;
}
.is-img{
    position: absolute;
    top: 10px;
    left: 10px;
}
.logo_subtitle{
    font-size: 16px;
    padding-top: 5px;
}

</style>






