<template>
    <span>
        <el-tooltip effect="dark" content="锁屏" placement="bottom">
            <i class="icon-bofangqi-suoping" @click="handleLock"></i>
        </el-tooltip>
        <el-dialog title="设置锁屏密码" :visible.sync="box" width="30%" append-to-body>
            <el-form :model="form" ref="form" label-width="80px">
                <el-form-item label="锁屏密码" prop="passwd" :rules="[{ required: true, message: '锁屏密码不能为空' }]">
                    <el-input v-model="form.passwd" placeholder="请输入锁屏密码"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="handleSetLock">确定</el-button>
            </span>
        </el-dialog>
    </span>
</template>
<script>
import { validatenull } from '@/utils/validate'
import { mapGetters } from 'vuex'
export default {
    name: 'nx-top-lock',
    data(){
        return {
            box: false,
            form: {
                passwd: ''
            }
        }
    },
    computed: {
        ...mapGetters(['lockPasswd'])
    },
    methods: {
        handleSetLock(){
            console.log(1234)
            this.$refs['form'].validate(valid => {
                if(valid){
                    console.log('yes')
                    this.$store.commit('SET_LOCK_PASSWD', this.form.passwd)
                    this.handleLock()
                }
            })
        },
        handleLock(){
            console.log('调用')
            console.log(this.lockPasswd)
            if(validatenull(this.lockPasswd)){
                this.box = true
                return
            }
            this.$store.commit('SET_LOCK')
            setTimeout(() => {
                console.log('hello')
                this.$router.push({ path: '/lock' })
            }, 100)
        }
    }
}
</script>






